<template>
    <div class="addEdit-block">
        <el-form
                class="detail-form-content"
                ref="ruleForm"
                :model="ruleForm"
                :rules="rules"
                label-width="auto"
                :style="{backgroundColor:addEditForm.addEditBoxColor}">
            <el-row>
                <input id="updateId" name="id" type="hidden">
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="meizhuangUuidNumber">
                       <el-input v-model="ruleForm.meizhuangUuidNumber"
                                 placeholder="" clearable  :readonly="ro.meizhuangUuidNumber"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="meizhuangUuidNumber">
                           <el-input v-model="ruleForm.meizhuangUuidNumber"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="meizhuangName">
                       <el-input v-model="ruleForm.meizhuangName"
                                 placeholder="" clearable  :readonly="ro.meizhuangName"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="meizhuangName">
                           <el-input v-model="ruleForm.meizhuangName"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
                <el-col :span="12">
                    <el-form-item class="upload" v-if="type!='info' && !ro.meizhuangPhoto" label="" prop="meizhuangPhoto">
                        <file-upload
                            tip=""
                            action="file/upload"
                            :limit="3"
                            :multiple="true"
                            :fileUrls="ruleForm.meizhuangPhoto?ruleForm.meizhuangPhoto:''"
                            @change="meizhuangPhotoUploadChange"
                        ></file-upload>
                    </el-form-item>
                    <div v-else>
                        <el-form-item v-if="ruleForm.meizhuangPhoto" label="" prop="meizhuangPhoto">
                            <img style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in (ruleForm.meizhuangPhoto || '').split(',')" :src="item" width="100" height="100">
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12">
                    <el-form-item class="select" v-if="type!='info'"  label="" prop="meizhuangTypes">
                        <el-select v-model="ruleForm.meizhuangTypes" :disabled="ro.meizhuangTypes" placeholder="" @change="meizhuangTypesChange">
                            <el-option
                                v-for="(item,index) in meizhuangTypesOptions"
                                v-bind:key="item.codeIndex"
                                :label="item.indexName"
                                :value="item.codeIndex">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="meizhuangValue">
                        <el-input v-model="ruleForm.meizhuangValue"
                            placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12" v-if="ruleForm.meizhuangTypes != null && ruleForm.meizhuangTypes != ''">
                    <el-form-item class="select" v-if="type!='info'"  label="" prop="meizhuangErjiTypes">
                        <el-select v-model="ruleForm.meizhuangErjiTypes" :disabled="ro.meizhuangErjiTypes" placeholder="">
                            <el-option
                                v-for="(item,index) in meizhuangErjiTypesOptions"
                                v-bind:key="item.codeIndex"
                                :label="item.indexName"
                                :value="item.codeIndex">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="meizhuangErjiValue">
                        <el-input v-model="ruleForm.meizhuangErjiValue"
                            placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
                <el-col :span="12">
                    <el-form-item class="select" v-if="type!='info'"  label="" prop="meizhuangStatusTypes">
                        <el-select v-model="ruleForm.meizhuangStatusTypes" :disabled="ro.meizhuangStatusTypes" placeholder="">
                            <el-option
                                v-for="(item,index) in meizhuangStatusTypesOptions"
                                v-bind:key="item.codeIndex"
                                :label="item.indexName"
                                :value="item.codeIndex">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item class="input" label="" prop="meizhuangStatusValue">
                        <el-input v-model="ruleForm.meizhuangStatusValue"
                            placeholder="" readonly></el-input>
                        </el-form-item>
                    </div>
                </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="meizhuangPrice">
                       <el-input v-model="ruleForm.meizhuangPrice"
                                 placeholder="" clearable  :readonly="ro.meizhuangPrice"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="meizhuangPrice">
                           <el-input v-model="ruleForm.meizhuangPrice"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="meizhuangKucunNumber">
                       <el-input v-model="ruleForm.meizhuangKucunNumber"
                                 placeholder="" clearable  :readonly="ro.meizhuangKucunNumber"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="meizhuangKucunNumber">
                           <el-input v-model="ruleForm.meizhuangKucunNumber"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="meizhuangOldMoney">
                       <el-input v-model="ruleForm.meizhuangOldMoney"
                                 placeholder="" clearable  :readonly="ro.meizhuangOldMoney"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="meizhuangOldMoney">
                           <el-input v-model="ruleForm.meizhuangOldMoney"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
               <el-col :span="12">
                   <el-form-item class="input" v-if="type!='info'"  label="" prop="meizhuangNewMoney">
                       <el-input v-model="ruleForm.meizhuangNewMoney"
                                 placeholder="" clearable  :readonly="ro.meizhuangNewMoney"></el-input>
                   </el-form-item>
                   <div v-else-if="type=='info'">
                       <el-form-item class="input" label="" prop="meizhuangNewMoney">
                           <el-input v-model="ruleForm.meizhuangNewMoney"
                                     placeholder="" readonly></el-input>
                       </el-form-item>
                   </div>
               </el-col>
                <el-col :span="24">
                    <el-form-item v-if="type!='info'"  label="" prop="meizhuangContent">
                        <editor style="min-width: 200px; max-width: 600px;"
                                v-model="ruleForm.meizhuangContent"
                                class="editor"
                                action="file/upload">
                        </editor>
                    </el-form-item>
                    <div v-else-if="type=='info'">
                        <el-form-item v-if="ruleForm.meizhuangContent" label="" prop="meizhuangContent">
                            <span v-html="ruleForm.meizhuangContent"></span>
                        </el-form-item>
                    </div>
                </el-col>
            </el-row>
            <el-form-item class="btn">
                <el-button v-if="type!='info'" type="primary" class="btn-success" @click="onSubmit"></el-button>
                <el-button v-if="type!='info'" class="btn-close" @click="back()"></el-button>
                <el-button v-if="type=='info'" class="btn-close" @click="back()"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
<script>
    import styleJs from "../../../utils/style.js";
    // url
    import { isNumber,isIntNumer,isEmail,isPhone, isMobile,isURL,checkIdCard } from "@/utils/validate";
    export default {
        data() {
            return {
                addEditForm:null,
                id: '',
                type: '',
                sessionTable : "",//
                role : "",//
                userId:"",//id
                ro:{
                    meizhuangUuidNumber: false,
                    meizhuangName: false,
                    meizhuangPhoto: false,
                    meizhuangTypes: false,
                    meizhuangErjiTypes: false,
                    meizhuangStatusTypes: false,
                    meizhuangPrice: false,
                    meizhuangKucunNumber: false,
                    meizhuangOldMoney: false,
                    meizhuangNewMoney: false,
                    meizhuangClicknum: false,
                    meizhuangContent: false,
                    shangxiaTypes: false,
                    meizhuangDelete: false,
                },
                ruleForm: {
                    meizhuangUuidNumber: new Date().getTime(),
                    meizhuangName: '',
                    meizhuangPhoto: '',
                    meizhuangTypes: '',
                    meizhuangErjiTypes: '',
                    meizhuangStatusTypes: '',
                    meizhuangPrice: '',
                    meizhuangKucunNumber: '',
                    meizhuangOldMoney: '',
                    meizhuangNewMoney: '',
                    meizhuangClicknum: '',
                    meizhuangContent: '',
                    shangxiaTypes: '',
                    meizhuangDelete: '',
                },
                meizhuangTypesOptions : [],
                meizhuangErjiTypesOptions : [],
                meizhuangStatusTypesOptions : [],
                shangxiaTypesOptions : [],
                rules: {
                   meizhuangUuidNumber: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   meizhuangName: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   meizhuangPhoto: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   meizhuangTypes: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangErjiTypes: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangStatusTypes: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangPrice: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangKucunNumber: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangOldMoney: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangNewMoney: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]{0,6}(\.[0-9]{1,2})?$/,
                                  message: '6,2',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangClicknum: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangContent: [
                              { required: true, message: '', trigger: 'blur' },
                          ],
                   shangxiaTypes: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                   meizhuangDelete: [
                              { required: true, message: '', trigger: 'blur' },
                              {  pattern: /^[0-9]*$/,
                                  message: '',
                                  trigger: 'blur'
                              }
                          ],
                }
            };
        },
        props: ["parent"],
        computed: {
        },
        created() {
            //
            this.sessionTable = this.$storage.get("sessionTable");
            this.role = this.$storage.get("role");
            this.userId = this.$storage.get("userId");


            if (this.role != ""){
            }
            this.addEditForm = styleJs.addStyle();
            this.addEditStyleChange()
            this.addEditUploadStyleChange()
            //
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=meizhuang_types`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.meizhuangTypesOptions = data.data.list;
                    }
                });
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=meizhuang_status_types`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.meizhuangStatusTypesOptions = data.data.list;
                    }
                });
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=shangxia_types`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.shangxiaTypesOptions = data.data.list;
                    }
                });


        },
        mounted() {
        },
        methods: {
            // 
            download(file){
                window.open(`${file}`)
            },
            // 
            init(id,type) {
                if (id) {
                    this.id = id;
                    this.type = type;
                }
                if(this.type=='info'||this.type=='else'){
                    this.info(id);
                }
                // 
                this.$http({
                    url:`${this.$storage.get("sessionTable")}/session`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        var json = data.data;
                    } else {
                        this.$message.error(data.msg);
                    }
                });
            },
            //
            meizhuangTypesChange(id){
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=meizhuang_erji_types&superId=${id}`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.ruleForm.meizhuangErjiTypes = null;
                        this.meizhuangErjiTypesOptions = data.data.list;
                    }
                });
            },
            //
            meizhuangErjiTypesChange(id){
                this.$http({
                    url:`dictionary/page?page=1&limit=100&sort=&order=&dicCode=meizhuang_erji_types&superId=${id}`,
                    method: "get"
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        this.meizhuangErjiTypesOptions = data.data.list;
                    }
                });
            },
            // 
            info(id) {
                let _this =this;
                _this.$http({
                    url: `meizhuang/info/${id}`,
                    method: 'get'
                }).then(({ data }) => {
                    if (data && data.code === 0) {
                        _this.ruleForm = data.data;
                        _this.meizhuangErjiTypesChange(data.data.meizhuangTypes);
                    } else {
                        _this.$message.error(data.msg);
                    }
                });
            },
            // 
            onSubmit() {
                this.$refs["ruleForm"].validate(valid => {
                    if (valid) {
                        this.$http({
                            url:`meizhuang/${!this.ruleForm.id ? "save" : "update"}`,
                            method: "post",
                            data: this.ruleForm
                        }).then(({ data }) => {
                            if (data && data.code === 0) {
                                this.$message({
                                    message: "",
                                    type: "success",
                                    duration: 1500,
                                    onClose: () => {
                                        this.parent.showFlag = true;
                                        this.parent.addOrUpdateFlag = false;
                                        this.parent.meizhuangCrossAddOrUpdateFlag = false;
                                        this.parent.search();
                                        this.parent.contentStyleChange();
                                    }
                                });
                            } else {
                                this.$message.error(data.msg);
                            }
                        });
                    }
                });
            },
            // uuid
            getUUID () {
                return new Date().getTime();
            },
            // 
            back() {
                this.parent.showFlag = true;
                this.parent.addOrUpdateFlag = false;
                this.parent.meizhuangCrossAddOrUpdateFlag = false;
                this.parent.contentStyleChange();
            },
            //
            meizhuangPhotoUploadChange(fileUrls){
                this.ruleForm.meizhuangPhoto = fileUrls;
                this.addEditUploadStyleChange()
            },

            addEditStyleChange() {
                this.$nextTick(()=>{
                    // input
                    document.querySelectorAll('.addEdit-block .input .el-input__inner').forEach(el=>{
                        el.style.height = this.addEditForm.inputHeight
                        el.style.color = this.addEditForm.inputFontColor
                        el.style.fontSize = this.addEditForm.inputFontSize
                        el.style.borderWidth = this.addEditForm.inputBorderWidth
                        el.style.borderStyle = this.addEditForm.inputBorderStyle
                        el.style.borderColor = this.addEditForm.inputBorderColor
                        el.style.borderRadius = this.addEditForm.inputBorderRadius
                        el.style.backgroundColor = this.addEditForm.inputBgColor
                    })
                    document.querySelectorAll('.addEdit-block .input .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.inputHeight
                        el.style.color = this.addEditForm.inputLableColor
                        el.style.fontSize = this.addEditForm.inputLableFontSize
                    })
                    // select
                    document.querySelectorAll('.addEdit-block .select .el-input__inner').forEach(el=>{
                        el.style.height = this.addEditForm.selectHeight
                        el.style.color = this.addEditForm.selectFontColor
                        el.style.fontSize = this.addEditForm.selectFontSize
                        el.style.borderWidth = this.addEditForm.selectBorderWidth
                        el.style.borderStyle = this.addEditForm.selectBorderStyle
                        el.style.borderColor = this.addEditForm.selectBorderColor
                        el.style.borderRadius = this.addEditForm.selectBorderRadius
                        el.style.backgroundColor = this.addEditForm.selectBgColor
                    })
                    document.querySelectorAll('.addEdit-block .select .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.selectHeight
                        el.style.color = this.addEditForm.selectLableColor
                        el.style.fontSize = this.addEditForm.selectLableFontSize
                    })
                    document.querySelectorAll('.addEdit-block .select .el-select__caret').forEach(el=>{
                        el.style.color = this.addEditForm.selectIconFontColor
                        el.style.fontSize = this.addEditForm.selectIconFontSize
                    })
                    // date
                    document.querySelectorAll('.addEdit-block .date .el-input__inner').forEach(el=>{
                        el.style.height = this.addEditForm.dateHeight
                        el.style.color = this.addEditForm.dateFontColor
                        el.style.fontSize = this.addEditForm.dateFontSize
                        el.style.borderWidth = this.addEditForm.dateBorderWidth
                        el.style.borderStyle = this.addEditForm.dateBorderStyle
                        el.style.borderColor = this.addEditForm.dateBorderColor
                        el.style.borderRadius = this.addEditForm.dateBorderRadius
                        el.style.backgroundColor = this.addEditForm.dateBgColor
                    })
                    document.querySelectorAll('.addEdit-block .date .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.dateHeight
                        el.style.color = this.addEditForm.dateLableColor
                        el.style.fontSize = this.addEditForm.dateLableFontSize
                    })
                    document.querySelectorAll('.addEdit-block .date .el-input__icon').forEach(el=>{
                        el.style.color = this.addEditForm.dateIconFontColor
                        el.style.fontSize = this.addEditForm.dateIconFontSize
                        el.style.lineHeight = this.addEditForm.dateHeight
                    })
                    // upload
                    let iconLineHeight = parseInt(this.addEditForm.uploadHeight) - parseInt(this.addEditForm.uploadBorderWidth) * 2 + 'px'
                    document.querySelectorAll('.addEdit-block .upload .el-upload--picture-card').forEach(el=>{
                        el.style.width = this.addEditForm.uploadHeight
                        el.style.height = this.addEditForm.uploadHeight
                        el.style.borderWidth = this.addEditForm.uploadBorderWidth
                        el.style.borderStyle = this.addEditForm.uploadBorderStyle
                        el.style.borderColor = this.addEditForm.uploadBorderColor
                        el.style.borderRadius = this.addEditForm.uploadBorderRadius
                        el.style.backgroundColor = this.addEditForm.uploadBgColor
                    })
                    document.querySelectorAll('.addEdit-block .upload .el-form-item__label').forEach(el=>{
                        el.style.lineHeight = this.addEditForm.uploadHeight
                        el.style.color = this.addEditForm.uploadLableColor
                        el.style.fontSize = this.addEditForm.uploadLableFontSize
                    })
                    document.querySelectorAll('.addEdit-block .upload .el-icon-plus').forEach(el=>{
                        el.style.color = this.addEditForm.uploadIconFontColor
                        el.style.fontSize = this.addEditForm.uploadIconFontSize
                        el.style.lineHeight = iconLineHeight
                        el.style.display = 'block'
                    })
                    // 
                    document.querySelectorAll('.addEdit-block .textarea .el-textarea__inner').forEach(el=>{
                        el.style.height = this.addEditForm.textareaHeight
                        el.style.color = this.addEditForm.textareaFontColor
                        el.style.fontSize = this.addEditForm.textareaFontSize
                        el.style.borderWidth = this.addEditForm.textareaBorderWidth
                        el.style.borderStyle = this.addEditForm.textareaBorderStyle
                        el.style.borderColor = this.addEditForm.textareaBorderColor
                        el.style.borderRadius = this.addEditForm.textareaBorderRadius
                        el.style.backgroundColor = this.addEditForm.textareaBgColor
                    })
                    document.querySelectorAll('.addEdit-block .textarea .el-form-item__label').forEach(el=>{
                        // el.style.lineHeight = this.addEditForm.textareaHeight
                        el.style.color = this.addEditForm.textareaLableColor
                        el.style.fontSize = this.addEditForm.textareaLableFontSize
                    })
                    // 
                    document.querySelectorAll('.addEdit-block .btn .btn-success').forEach(el=>{
                        el.style.width = this.addEditForm.btnSaveWidth
                        el.style.height = this.addEditForm.btnSaveHeight
                        el.style.color = this.addEditForm.btnSaveFontColor
                        el.style.fontSize = this.addEditForm.btnSaveFontSize
                        el.style.borderWidth = this.addEditForm.btnSaveBorderWidth
                        el.style.borderStyle = this.addEditForm.btnSaveBorderStyle
                        el.style.borderColor = this.addEditForm.btnSaveBorderColor
                        el.style.borderRadius = this.addEditForm.btnSaveBorderRadius
                        el.style.backgroundColor = this.addEditForm.btnSaveBgColor
                    })
                    // 
                    document.querySelectorAll('.addEdit-block .btn .btn-close').forEach(el=>{
                        el.style.width = this.addEditForm.btnCancelWidth
                        el.style.height = this.addEditForm.btnCancelHeight
                        el.style.color = this.addEditForm.btnCancelFontColor
                        el.style.fontSize = this.addEditForm.btnCancelFontSize
                        el.style.borderWidth = this.addEditForm.btnCancelBorderWidth
                        el.style.borderStyle = this.addEditForm.btnCancelBorderStyle
                        el.style.borderColor = this.addEditForm.btnCancelBorderColor
                        el.style.borderRadius = this.addEditForm.btnCancelBorderRadius
                        el.style.backgroundColor = this.addEditForm.btnCancelBgColor
                    })
                })
            },
            addEditUploadStyleChange() {
                this.$nextTick(()=>{
                    document.querySelectorAll('.addEdit-block .upload .el-upload-list--picture-card .el-upload-list__item').forEach(el=>{
                        el.style.width = this.addEditForm.uploadHeight
                        el.style.height = this.addEditForm.uploadHeight
                        el.style.borderWidth = this.addEditForm.uploadBorderWidth
                        el.style.borderStyle = this.addEditForm.uploadBorderStyle
                        el.style.borderColor = this.addEditForm.uploadBorderColor
                        el.style.borderRadius = this.addEditForm.uploadBorderRadius
                        el.style.backgroundColor = this.addEditForm.uploadBgColor
                    })
                })
            },
        }
    };
</script>
<style lang="scss">
.editor{
  height: 500px;

  & /deep/ .ql-container {
	  height: 310px;
  }
}
.amap-wrapper {
  width: 100%;
  height: 500px;
}
.search-box {
  position: absolute;
}
.addEdit-block {
	margin: -10px;
}
.detail-form-content {
	padding: 12px;
